export default {
	data() {
		return {
			id: 0,
			detail: {}
		};
	},
	methods: {
		async getDetail() {
			const res = await this.$myRequest({
				url: 'api/getnew/' + this.id
			})
			this.detail = res.data.message[0]
		}
	},
	onLoad(option) {
		this.id = option.id
		this.getDetail()
	}
}
